// Ïä§ÏôÄÏù¥ÌîÑ Í∞ÄÎä•Ìïú Î©îÎâ¥ ÏòµÏÖò
.menu-options.ios-grid {
  cursor: grab;
  user-select: none;
  touch-action: pan-y pinch-zoom;
  
  &:active {
    cursor: grabbing;
  }
  
  // ÎìúÎûòÍ∑∏ Ï§ë ÌÖçÏä§Ìä∏ ÏÑ†ÌÉù Î∞©ÏßÄ
  * {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
  }
}

// ÌéòÏù¥ÏßÄ Ïù∏ÎîîÏºÄÏù¥ÌÑ∞ Í∞úÏÑ†
.page-indicator {
  display: flex;
  justify-content: center;
  gap: 8px;
  padding: 12px 0;
  
  .dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.3);
    cursor: pointer;
    transition: all 0.3s ease;
    
    &.active {
      background: var(--accent-color);
      width: 24px;
      border-radius: 4px;
    }
    
    &:hover {
      transform: scale(1.2);
      opacity: 0.8;
    }
  }
}

// Îã§ÌÅ¨Î™®Îìú Ïù∏ÎîîÏºÄÏù¥ÌÑ∞
:host-context(.dark-theme) {
  .page-indicator .dot {
    background: rgba(255, 255, 255, 0.3);
    
    &.active {
      background: var(--accent-color);
    }
  }
}

// Pulse Ïï†ÎãàÎ©îÏù¥ÏÖò
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
}
